body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;
  --font-manrope: Manrope;

  /* font sizes */
  --font-size-17xl: 36px;
  --font-size-10xl: 29px;
  --font-size-3xl: 22px;
  --font-size-sm: 14px;
  --font-size-45xl: 64px;
  --font-size-32xl: 51px;
  --font-size-19xl: 38px;
  --font-size-5xl: 24px;
  --font-size-lgi: 19px;
  --font-size-lg: 18px;

  /* Colors */
  --color-white: #fff;
  --color-skyblue-100: #71dcee;
  --color-skyblue-200: #49bfd3;
  --color-darkslateblue-100: #04325e;
  --color-palevioletred: #f38db6;
  --color-yellow: #f5ed30;
  --color-black: #000;
  --color-salmon: #d7704e;
  --color-mediumseagreen: #27b573;
  --color-peru: #b37732;
  --color-darkslategray: #2a3e55;
  --color-paleturquoise: #90e6f4;

  /* Gaps */
  --gap-96xl: 115px;
  --gap-38xl: 57px;
  --gap-10xl: 29px;
  --gap-8xl: 27px;
  --gap-xl: 20px;

  /* Paddings */
  --padding-xl: 20px;
  --padding-32xl: 51px;
  --padding-15xl: 34px;
  --padding-12xs: 1px;
  --padding-3xs: 10px;
  --padding-5xl: 24px;
  --padding-21xl: 40px;
  --padding-6xs: 7px;

  /* Border radiuses */
  --br-3xs: 10px;
}
